.icon-scroll,
.icon-scroll:before {
  position: absolute;
  left: 50%;
}

.icon-scroll {
  width: 25px;
  height: 40px;
  margin-left: -15px;
  bottom: 3rem;
  margin-top: -35px;
  box-shadow: inset 0 0 0 2px $white;
  border-radius: 25px;
}

.icon-scroll:before {
  content: "";
  width: 5px;
  height: 5px;
  background: $white;
  margin-left: -2px;
  top: 8px;
  border-radius: 4px;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-name: scroll;
}

@keyframes scroll {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(15px);
  }
}
