.header {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../../img/home-bg.jpg") no-repeat center;
  background-size: cover;
  background-attachment: fixed;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
  &__title {
    width: 100%;
    h1 {
      font-family: "Lato Regular";
      font-size: 50px;
      color: $white;
      margin: 1.2rem 0;
    }
    h2 {
      color: $header-text;
      font-size: 26px;
      font-family: "Lato Regular";
      letter-spacing: 1px;
      height: 1rem;
    }
    img {
      height: 11rem;
      border-radius: 50%;
      border: 3px solid $white;
      margin-bottom: 0.5rem;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.52);
    }
  }
}
