.footer {
  padding-top: 7rem;
  padding-bottom: 2rem;
  background-color: $footer-bg;
  &__title {
    font-size: 3rem;
    font-family: "Lato Bold";
    width: 80%;
    margin: 0 auto;
    color: $footer-title;
    margin-bottom: 1.5rem;
    a {
      text-decoration: none;
      color: $black;
      display: inline-block;
      margin-left: 1rem;
    }
  }
  &__text {
    width: 80%;
    margin: 0 auto;
    padding-right: 29rem;
    font-family: "Lato Light";
    font-size: 1rem;
    line-height: 1.5rem;
    color: $black;
    margin-bottom: 5rem;
  }
  &__socials {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 80%;
    margin: 0 auto;
    &_item {
      border: 1px solid $black;
      padding: 2rem 1rem;
      width: 20%;
      display: flex;
      align-items: center;
      align-content: center;
      justify-content: space-around;
      transition: all 0.3s ease-in-out;
      &--title {
        font-family: "Lato Bold";
        font-size: 1.2rem;
      }
      &--icon {
        transform: translateY(5%);
      }
      &:hover {
        background-color: $black;
        color: $white;
        cursor: pointer;
      }
    }
  }
  &__rights {
    text-align: center;
    padding-right: 0 !important;
    font-family: "Lato Light";
    font-size: 1rem;
    line-height: 1.5rem;
    color: $black;
    margin-top: 7rem;
  }
}
