.navbar {
  z-index: 9999;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  padding: 2rem 0;
  &__brand {
    flex-basis: 40%;
    text-align: center;
    color: $white;
    font-size: 1.2rem;
    font-family: "Lato Bold";
    color: $navbar-text;
  }
  &__menu {
    flex-basis: 60%;
    padding: 0 10rem;
    color: $navbar-text;
    font-family: "Lato Bold";
    font-size: 0.9rem;
    ul {
      display: flex;
      align-items: center;
      justify-content: space-around;
      list-style: none;
    }
  }
}
